.gallery {
  background: linear-gradient(to top, white 31.1165%, white 31.1165%, #fff5f6 39.767%);
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 767px) {
  .gallery .container {
    margin-left: -20px;
  }
}
.gallery__image {
  display: block;
  @media screen and (max-width: 480px) {
    margin-left: -18px;
    display: block;
  }
  @media screen and (min-width: 768px) {
    display: block;
    margin-left: -9px;
  }
  @media screen and (min-width: 1280px) {
    display: block;
    margin-right: auto;
    margin-left: auto;
  }
}
